      </van-grid-item>
      <van-grid-item @click="message">
        <img src="../../assets/img/menu4.png" alt="" />
        <p>留言反馈</p>
      </van-grid-item>
      <van-grid-item @click="video">
        <img src="../../assets/img/menu5.png" alt="" />
        <p>视频专区</p>
      </van-grid-item>
      <van-grid-item @click="contact">
        <img src="../../assets/img/menu6.png" alt="" />
