<script>
import GTabbar from '../tabbar'

export default {
  data () {
    return {
      images: [
        'https://img.yzcdn.cn/vant/apple-1.jpg',
        'https://img.yzcdn.cn/vant/apple-2.jpg'
      ]
    }
  },
  methods: {
    async getSwipeList () {
      var { data: res } = await this.$http.get('/api/getlunbo')
      console.log(res)
      if (res.status !== 0) {
        return this.$toast('加载轮播图失败')
      }
      this.swipeList = res.message
    },
    news () {
      this.$router.push('/news')
    },
    photo () {
      this.$router.push('/photo_sharing')
    },
    goods () {
      this.$router.push('/goods/list')
    },
    message () {
      this.$router.push('/message')
    },
    video () {
      this.$router.push('/video')
    },
    contact () {
      this.$router.push('/contact')
    }
  },
  components: {
    GTabbar
  }
}
</script>
